h1 {
	font-family: OS_B;
	font-size: 35px;
	color: $blue;
}
header {
	nav {
		&>ul {
			list-style-type: none;
			font-family: OS_I;
			@include flex(space-evenly, center);
			@include respond(laptop) {
				@include flex(center, center, column, wrap);
			}
			&>li {
				font-size: 20px;
				cursor: pointer;
				&:not(:last-child) {
					margin-right: 15px;
				}
				&:hover {
					color: $red;
				}
			}
		}
	}
}
// scss
.square {
	text-align: center;
	text-transform: uppercase;
	height: 150px;
	@include background('star_daft.jpg', center, cover);
	.text_color {
		color: $green;
		margin: 10px;
		.exemple_2 & {
			color: $orange;
		}
	}
}
// css
.square {
	text-align: center;
	text-transform: uppercase;
}
.square .text_color {
	color: $green;
	margin: 10px;
}
.exemple_2 .square .text_color {
	color: $orange;
}

// placeholder
.back_color {
	background-color: $green;
	height: 300px;
	width: 300px;
	position: relative;
	margin: 50px auto;
	.text_abs_center {
		@extend %center_absolute;
	}
}
// heritage
.bg_black {
	background-color: #000;
	@include flex(center, center);
}
.bg_white {
	background-color: #fff;
	@include flex(center, center);
} 
.bg_extend {
	@extend .bg_black;
}
.bg_extend2 {
	@extend .bg_white;
}
.size300x300 {
	height: 300px;
	width: 300px;
	margin: 50px auto;
}
.size200x200 {
	height: 200px;
	width: 200px;
}
.size100x100 {
	height: 100px;
	width: 100px;
}
.size50x50 {
	height: 50px;
	width: 50px;
}
.size20x20 {
	height: 20px;
	width: 20px;
}